import React, { useEffect, useState } from "react"
import MyNavBar from "../../common/MyNavBar"
import MyHttp from "../../helper/MyHttp"
import { AdminI } from "./admin-interfaces"
import MyInput from "../../common/MyInput"
import MyRadioBtn from "../../common/MyRadioBtn"
import { Modal, Button } from "react-bootstrap"

interface ViewAdminI {
    data: AdminI[]
    editId: number
    error: string
    editUsername: string
    editRole: string
    show: boolean
}

class ViewAdmin extends React.Component<any, ViewAdminI> {

    constructor(props: any) {
        super(props)

        this.state = {
            data: [],
            editId: 0,
            error: "",
            editUsername: "",
            editRole: "",
            show: false,
        }

        this.handleClose = this.handleClose.bind(this)
        this.saveData = this.saveData.bind(this)
    }


    async getData() {
        try {
            const res = await MyHttp.get("/api/admin")

            if (res.status === 200) {
                console.log(res.data);
                this.setState({
                    data: res.data
                })
            }
        } catch (error) {
            console.log(error.message);
        }
    }

    setEditData(id: number) {
        this.setState({
            editId: id
        })
        const gotData: AdminI | undefined = this.state.data.find((d: AdminI) => d.id === id)
        if (gotData === undefined || gotData === null) {
            console.log("Wrong Id");
            return
        }

        this.setState({
            show: true,
            editUsername: gotData!.username,
            editRole: gotData!.role
        })

    }

    async  saveData() {
        try {
            const dataToSend: AdminI = {
                id: this.state.editId,
                username: this.state.editUsername,
                role: this.state.editRole
            }
            const res = await MyHttp.put("/api/admin", dataToSend)
            const gotData: AdminI = res.data
            const dataIndex: number = this.state.data.findIndex((d: AdminI) => d.id === gotData.id)
            console.log(gotData);

            this.state.data[dataIndex] = gotData
            console.log(this.state.data);
            this.setState({
                data: this.state.data,
                show: false
            })
            console.log(this.state.data);

        } catch (error) {
            console.log(error.message);

        }
    }

    renderData() {
        const out = (this.state.data || []).map((d: AdminI) => {
            return (
                <tr key={d.id!}>
                    <td>{d.id! + 1}</td>
                    <td>{d.username}</td>
                    <td>{d.role}</td>
                    <td><button className="btn btn-warning" onClick={() => {
                        this.setEditData(d.id!)

                    }}>Edit</button></td>
                </tr>
            )
        })
        return out
    }

    componentDidMount() {
        this.getData()
    }

    handleClose() {
        this.setState({
            show: false
        })
    }

    render() {
        return (
            <React.Fragment>
                <MyNavBar />

                <div className="container">
                    <div className="jumbotron">
                        <h1>View/Edit</h1>
                        <Modal show={this.state.show} onHide={this.handleClose}>
                            <Modal.Header closeButton>
                                <Modal.Title>Modal heading</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <MyInput label="Username" value={this.state.editUsername} onChange={(e: React.ChangeEvent<HTMLInputElement>) => this.setState({ editUsername: e.currentTarget.value })} />
                                <label htmlFor="">Role:</label><br />
                                <MyRadioBtn name="role" onChange={(e: React.ChangeEvent<HTMLInputElement>) => this.setState({ editRole: e.currentTarget.value })} label="Admin" value="admin" checked={(this.state.editRole === "admin")} />
                                <MyRadioBtn name="role" onChange={(e: React.ChangeEvent<HTMLInputElement>) => this.setState({ editRole: e.currentTarget.value })} label="Sub-Admin" value="sub-admin" checked={(this.state.editRole === "sub-admin")} />
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant="secondary" onClick={this.handleClose}>
                                    Close
                                </Button>
                                <Button variant="primary" onClick={this.saveData}>
                                    Save Changes
                                </Button>
                            </Modal.Footer>
                        </Modal>
                        <table className="table">
                            <thead>
                                <tr>
                                    <th scope="col">#</th>
                                    <th scope="col">Username</th>
                                    <th scope="col">Role</th>
                                    <th scope="col">Edit</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.renderData()}
                            </tbody>
                        </table>
                    </div>
                    <p className="text-danger">{this.state.error}</p>
                </div>
            </React.Fragment >

        )
    }

}

export default ViewAdmin