import React, { useEffect, useState } from "react"
import MyNavBar from "../../common/MyNavBar"
import MyHttp from "../../helper/MyHttp"
import MyInput from "../../common/MyInput"
import MyRadioBtn from "../../common/MyRadioBtn"
import { Modal, Button } from "react-bootstrap"
import EventI from "./event-interface"
import MyTextArea from "../../common/MyTextarea"
import  moment from 'moment';

interface ViewEventI extends EventI {
    data: EventI[]
    show: boolean
    erorr: string
}

class ViewEvent extends React.Component<any, ViewEventI> {

    getDate(d: Date): string{
        return moment(d).format("YYYY-MM-DD")
    }

    constructor(props: any) {
        super(props)

        this.state = {
            data: [],
            id: 0,
            name: "",
            start_date: "",
            end_date: "",
            location: "",
            entry_procedure: "",
            exhibition_house: "",
            description: "",
            show: false,
            erorr: ""
        }

        this.handleClose = this.handleClose.bind(this)
        this.saveData = this.saveData.bind(this)
        this.onChange = this.onChange.bind(this)
    }


    async getData() {
        try {
            const res = await MyHttp.get("/api/event")

            if (res.status === 200) {
                console.log(res.data);
                if(res.data !== null){
                    let data: EventI[] = res.data
                    // var options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };

                    data = data.map((d) => {
                        //@ts-ignore
                        const a = new Date(d.start_date)
                        d.start_date = this.getDate(a)

                        const b = new Date(d.end_date)
                        d.end_date = this.getDate(b)// b.toLocaleDateString("en-us")
                        return d
                    })
                
                    this.setState({
                        data: res.data
                    })
                }
            }
        } catch (error) {
            console.log(error.message);
        }
    }

    setEditData(id: number) {
        this.setState({
            id: id
        })
        const gotData: EventI | undefined = this.state.data.find((d: EventI) => d.id === id)
        if (gotData === undefined || gotData === null) {
            console.log("Wrong Id");
            return
        }

        this.setState({
            show: true,
            name: gotData.name,
            start_date: gotData.start_date,
            end_date: gotData.end_date,
            location: gotData.location,
            entry_procedure: gotData.entry_procedure,
            exhibition_house: gotData.exhibition_house,
            description: gotData.description,
        })

    }

    async  saveData() {
        try {
            const dataToSend: EventI = {
                id: this.state.id,
                name: this.state.name,
                start_date: this.state.start_date,
                end_date: this.state.end_date,
                location: this.state.location,
                entry_procedure: this.state.entry_procedure,
                exhibition_house: this.state.exhibition_house,
                description: this.state.description,
            }
            const res = await MyHttp.put("/api/event", dataToSend)
            const gotData: EventI = res.data
            const dataIndex: number = this.state.data.findIndex((d: EventI) => d.id === gotData.id)
            console.log(gotData);

            this.state.data[dataIndex] = gotData
            console.log(this.state.data);
            this.setState({
                data: this.state.data,
                show: false
            })
            console.log(this.state.data);

        } catch (error) {
            console.log(error.message);

        }
    }

    renderData() {
        const out = (this.state.data || []).map((d: EventI, i: number) => {
            return (
                <tr key={d.id!}>
                    <td>{i + 1}</td>
                    <td>{d.name}</td>
                    <td>{d.description}</td>
                    <td>{d.start_date}</td>
                    <td>{d.end_date}</td>
                    <td>{d.location}</td>
                    <td>{d.entry_procedure}</td>
                    <td>{d.exhibition_house}</td>
                    <td><button className="btn btn-warning" onClick={() => {
                        this.setEditData(d.id!)

                    }}>Edit</button></td>
                </tr>
            )
        })
        return out
    }

    componentDidMount() {
        this.getData()
    }

    handleClose() {
        this.setState({
            show: false
        })
    }

    onChange(e: React.ChangeEvent<HTMLInputElement>) {
        console.log(e.currentTarget.value);
        
        this.setState({
            ...this.state,
            [e.currentTarget.name]: e.currentTarget.value
        })
    }

    render() {
        return (
            <React.Fragment>
                <MyNavBar />
                <div className="container">
                    <div className="jumbotron">
                        <h1>View/Edit</h1>
                        <Modal show={this.state.show} onHide={this.handleClose}>
                            <Modal.Header closeButton>
                                <Modal.Title>Modal heading</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <MyInput label="Name" name="name" value={this.state.name} onChange={this.onChange} />
                                <MyTextArea label="Description" name="description" value={this.state.description} onChange={this.onChange} />
                                <MyTextArea label="Entry Procedure" name="entry_procedure" value={this.state.entry_procedure} onChange={this.onChange} />
                                <MyInput label="Exhibition House" name="exhibition_house" value={this.state.exhibition_house} onChange={this.onChange} />

                                <MyInput label="Start Date" type="date" name="start_date" value={this.state.start_date as string} onChange={this.onChange} />
                                <MyInput label="End Date" type="date" name="end_date" value={this.state.end_date as string} onChange={this.onChange} />
                                <MyInput label="Location" name="location" value={this.state.location} onChange={this.onChange} />
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant="secondary" onClick={this.handleClose}>
                                    Close
                                </Button>
                                <Button variant="primary" onClick={this.saveData}>
                                    Save Changes
                                </Button>
                            </Modal.Footer>
                        </Modal>
                        <table className="table">
                            <thead>
                                <tr>
                                    <th scope="col">#</th>
                                    <th scope="col">Name</th>
                                    <th scope="col">Desc</th>
                                    <th scope="col">Start Date</th>
                                    <th scope="col">End Date</th>
                                    <th scope="col">Location</th>
                                    <th scope="col">Entry Procedure</th>
                                    <th scope="col">Exhibition Hose</th>
                                    <th scope="col">Edit</th>
                                </tr>
                            </thead>
                            <tbody>
                                {this.renderData()}
                            </tbody>
                        </table>
                    </div>
                    <p className="text-danger">{this.state.erorr}</p>
                </div>
            </React.Fragment >

        )
    }

}

export default ViewEvent