import React from "react"
import { Form } from "react-bootstrap"

export interface MyTextAreaI {
    name?: string
    value?: string
    onChange: ((event: React.ChangeEvent<HTMLInputElement>) => void) | undefined
    id?: string
    label?: string
    placeholder?: string
    type?: string
}

const MyTextArea = (props: MyTextAreaI) => {

    return (
        <React.Fragment>
            <Form.Group controlId="exampleForm.ControlTextarea1">
                <Form.Label>{props.label}:</Form.Label>
                <Form.Control name={props.name} value={props.value} onChange={props.onChange} as="textarea" rows="3" />
            </Form.Group>
        </React.Fragment>
    )
}

export default MyTextArea
