import React, { useState, FormEvent } from "react"
import Axios from "axios"
import { AdminI } from "./admin-interfaces"
import MyInput from "../../common/MyInput"
import MyNavBar from "../../common/MyNavBar"
import MyRadioBtn from "../../common/MyRadioBtn"
import MyHttp from "../../helper/MyHttp"

const CreateAdmin = () => {
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [role, setRole] = useState('admin')
    const [error, setError] = useState('')
    const [message, setMessage] = useState('')

    async function postData(e: FormEvent<HTMLFormElement>) {
        e.preventDefault()
        setMessage('')
        setError('')
        if (username.trim() === '' && password.trim() === '' && role.trim() === '') {
            setError("Enter all details")
            return
        }
        const data: AdminI = {
            username,
            password,
            role
        }
        const res = await MyHttp.post("/api/login/create", data)
        if (res.status == 200) {
            setMessage('Added successfully.')
            setUsername('')
            setPassword('')
            setRole('admin')
        } else {
            const error: string = res.data.message
            if (!error) {
                setError('Error')
                return
            }
            setError(error)
        }
    }
    return (
        <React.Fragment>
            <MyNavBar />
            <div className="container">
                <div className="jumbotron">
                    <form onSubmit={postData}>
                        <h1>Add Admin</h1>
                        <MyInput value={username} onChange={(e: React.ChangeEvent<HTMLInputElement>) => setUsername(e.currentTarget.value)} label="Username" />
                        <MyInput value={password} onChange={(e: React.ChangeEvent<HTMLInputElement>) => setPassword(e.currentTarget.value)} label="Password" />
                        <p>Role:</p>
                        <MyRadioBtn name="role" onChange={(e:  React.ChangeEvent<HTMLInputElement>) => setRole(e.currentTarget.value)} label="Admin" value="admin" checked={(role === "admin")} />
                        <MyRadioBtn name="role" onChange={(e:  React.ChangeEvent<HTMLInputElement>) => setRole(e.currentTarget.value)} label="Sub-Admin" value="sub-admin"  checked={(role === "sub-admin")}/>
                        <br />
                        <br />
                        <button className="btn btn-success" type="submit">Submit</button>
                        <p className="text-danger">{error}</p>
                        <p className="text-success">{message}</p>
                    </form>
                </div>
            </div>
        </React.Fragment>
    )
}

export default CreateAdmin