
import React, { useState, FormEvent } from "react"
import MyNavBar from "../../common/MyNavBar"
import MyInput from "../../common/MyInput"
import { Form } from "react-bootstrap"
import MyTextArea from "../../common/MyTextarea"
import MyHttp from "../../helper/MyHttp"

const AddEvent = () => {
    const [name, setName] = useState('')
    const [description, setDescription] = useState('')
    const [entryProcedure, setEntryProcedure] = useState('')
    const [exhibitionHouse, setExhibitionHouse] = useState('')
    const [startDate, setStartDate] = useState('')
    const [endDate, setEndDate] = useState('')
    const [location, setLocation] = useState('')
    const [error, setError] = useState('')
    const [message, setMessage] = useState('')
    let myform: HTMLFormElement | null


    async function submitData(e: FormEvent<HTMLFormElement>) {
        e.preventDefault()
        if (entryProcedure.trim() === '' && description.trim() === '') {
            setError("Enter all details")
            return
        }

        const data = {
            name,
            description,
            entryProcedure,
            exhibitionHouse,
            startDate,
            endDate,
            location,
        }
        try {
            const res = await MyHttp.post("/api/event", data)
            if (res.status === 200) {
                console.log(res.data);
                setMessage('Added successfully.')
                // @ts-ignore
                document!.getElementById('my-form')!.reset()
            } else {
                setError(res.data.message)
            }

        } catch (error) {
            setError(error.message)
        }


    }

    return (
        <React.Fragment>
            <MyNavBar />
            <div className="container">
                <div className="jumbotron">
                    <h1>Add</h1>
                    <form onSubmit={submitData} id="my-form">

                        <MyInput label="Name" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setName(e.currentTarget.value)} />
                        <MyTextArea label="Description" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setDescription(e.currentTarget.value)} />
                        <MyTextArea label="Entry Procedure" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setEntryProcedure(e.currentTarget.value)} />
                        <MyInput label="Exhibition House" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setExhibitionHouse(e.currentTarget.value)} />

                        <MyInput label="Start Date" type="date" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setStartDate(e.currentTarget.value)} />
                        <MyInput label="End Date" type="date" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setEndDate(e.currentTarget.value)} />
                        <MyInput label="Location" onChange={(e: React.ChangeEvent<HTMLInputElement>) => setLocation(e.currentTarget.value)} />
                        <button type="submit" className="btn btn-success">Submit</button>
                        <p className="text-danger">{error}</p>
                        <p className="text-success">{message}</p>

                    </form>

                </div>
            </div>
        </React.Fragment>

    )
}

export default AddEvent