import React from "react"

export interface MyRadioBtnI {
    name: string
    checked: boolean
    label: string
    value: string
    onChange: ((event: React.ChangeEvent<HTMLInputElement>) => void) | undefined
}

const MyRadioBtn = (props: MyRadioBtnI) => {

    return (
        <div className="form-check-inline">
            <label className="form-check-label">
                <input type="radio" onChange={props.onChange} value={props.value} className="form-check-input" name={props.name} checked={props.checked} />{props.label}
            </label>
        </div>
    )
}

export default MyRadioBtn